<template lang="pug">
.curso-main-container.pb-3
  BannerInterno
  .container.tarjeta.tarjeta--blanca.p-4.p-md-5
    .titulo-principal.color-acento-contenido
      .titulo-principal__numero
        span 2
      h1 #[em Hacking] tico

    .cajon-b.color-primario.cajon-end.p-3.mb-5(data-aos="fade-right")
      .row.justify-content-between
        .col-lg-10
          p.mb-0 La piratera tica implica un intento autorizado de obtener acceso no autorizado a un sistema informtico, aplicacin o datos. Realizar un #[em hack] tico implica duplicar estrategias y acciones de atacantes malintencionados. Esta prctica ayuda a identificar vulnerabilidades de seguridad que luego pueden resolverse antes de que un atacante malicioso tenga la oportunidad de explotarlas.
        .col-lg-2.col-3
          img(src='@/assets/curso/tema2/img01.svg'  alt="")

    .mb-4.d-flex
      .d-flex.flex-wrap.align-items-center.pe-4(style="background-color: #40327B; border-radius: 0 15px 15px 0;" data-aos="fade-right")
        img(src='@/assets/curso/temas/img03.svg' style="max-width: 40px;" alt="")
        .h3.col.p-2.text-white.mb-0 Qu es un #[em hacker] tico?

    .BGIMG03.p-lg-5.p-4.mb-5(data-aos="flip-up")
      .row.justify-content-around.align-items-center
        .col-lg-2.col-4.mb-lg-0.mb-4
          img(src='@/assets/curso/tema2/img02.svg' alt="")
        .col-lg-7
          p.mb-0 Tambin conocidos como sombreros blancos, los #[em hackers] ticos son expertos en seguridad que realizan estas evaluaciones de seguridad. El trabajo proactivo que realizan ayuda a mejorar la postura de seguridad de una organizacin. Con la aprobacin previa de la organizacin o del propietario del activo de TI, la misin de la piratera tica es opuesta a la de la piratera maliciosa.

    .mb-4.d-flex
      .d-flex.flex-wrap.align-items-center.pe-4(style="background-color: #40327B; border-radius: 0 15px 15px 0;" data-aos="fade-right")
        img(src='@/assets/curso/temas/img03.svg' style="max-width: 40px;" alt="")
        .h3.col.p-2.text-white.mb-0 Cules son los conceptos clave del #[em hacking] tico?

    p Los expertos en piratera siguen cuatro conceptos clave de protocolo, a saber:

    .row.justify-content-center.mb-4
      .col-xl-3.col-md-6.col-8.mb-4(data-aos="fade-right")
        figure.mb-4
          img(src='@/assets/curso/tema2/img03.svg' alt="")
        p.mb-0 Obtenga la aprobacin adecuada antes de acceder y realizar una evaluacin de seguridad.
      .col-xl-3.col-md-6.col-8.mb-4(data-aos="flip-right")
        figure.mb-4
          img(src='@/assets/curso/tema2/img04.svg' alt="")
        p.mb-0 Determine el alcance de la evaluacin para que el trabajo del #[em hacker] tico siga siendo legal y dentro de los lmites aprobados de la organizacin.
      .col-xl-3.col-md-6.col-8.mb-4(data-aos="flip-left")
        figure.mb-4
          img(src='@/assets/curso/tema2/img05.svg' alt="")
        p.mb-0 Notifique a la organizacin de todas las vulnerabilidades descubiertas durante la evaluacin. Proporcione consejos de remediacin para resolver estas vulnerabilidades.
      .col-xl-3.col-md-6.col-8.mb-4(data-aos="fade-left")
        figure.mb-4
          img(src='@/assets/curso/tema2/img06.svg' alt="")
        p.mb-0 Dependiendo de la confidencialidad de los datos, los #[em hackers] ticos pueden tener que aceptar un acuerdo de confidencialidad, adems de otros trminos y condiciones requeridos por la organizacin evaluada.

    .mb-4.d-flex
      .d-flex.flex-wrap.align-items-center.pe-4(style="background-color: #40327B; border-radius: 0 15px 15px 0;" data-aos="fade-right")
        img(src='@/assets/curso/temas/img03.svg' style="max-width: 40px;" alt="")
        .h3.col.p-2.text-white.mb-0 En qu se diferencian los #[em hackers] ticos de los #[em hackers] maliciosos?

    p Los #[em hackers] ticos utilizan su conocimiento para asegurar y mejorar la tecnologa de las organizaciones. Brindan un servicio esencial al buscar vulnerabilidades que pueden conducir a una brecha de seguridad. En el siguiente video se puede conocer la diferente entre estos dos tipos de #[em hackers].

    figure.mb-5(data-aos="zoom-in")
      .video
        iframe(width="560" height="315" src="https://www.youtube.com/embed/b4X4Rh4eNyU" title="YouTube video player" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen)
      figcaption Video. En qu se diferencian los hackers ticos de los hackers maliciosos?

    .mb-4.d-flex
      .d-flex.flex-wrap.align-items-center.pe-4(style="background-color: #40327B; border-radius: 0 15px 15px 0;" data-aos="fade-right")
        img(src='@/assets/curso/temas/img03.svg' style="max-width: 40px;" alt="")
        .h3.col.p-2.text-white.mb-0 Qu problemas identifica el #[em hacking]?

    .row.justify-content-center.align-items-center.mb-5
      .col-lg-9.order-lg-1.order-2(data-aos="fade-right")
        p Al evaluar la seguridad de los activos de TI de una organizacin, la piratera tica tiene como objetivo imitar a un atacante. Al hacerlo, buscan vectores de ataque contra el objetivo. El objetivo inicial es realizar un reconocimiento, obteniendo la mayor cantidad de informacin posible. Una vez que el #[em hacker] tico recopila suficiente informacin, la usa para buscar vulnerabilidades contra el activo. Realizan esta evaluacin con una combinacin de pruebas automatizadas y manuales. Incluso los sistemas sofisticados pueden tener tecnologas de contramedidas complejas que pueden ser vulnerables. No se detienen en descubrir vulnerabilidades. Los #[em hackers] ticos usan #[em exploit] contra las vulnerabilidades para probar cmo un atacante malintencionado podra explotarlas.
        p Algunas de las vulnerabilidades ms comunes descubiertas por #[em hackers] ticos incluyen:
        ul.lista-ul--color
          li
            i.fas.fa-check-circle(style="color: #583AE2")
            span Ataques de inyeccin.
          li
            i.fas.fa-check-circle(style="color: #583AE2")
            span Autenticacin rota.
          li
            i.fas.fa-check-circle(style="color: #583AE2")
            span Configuraciones incorrectas de seguridad.
          li
            i.fas.fa-check-circle(style="color: #583AE2")
            span Uso de componentes con vulnerabilidades conocidas.
          li
            i.fas.fa-check-circle(style="color: #583AE2")
            span Exposicin de datos confidenciales.
        p.mb-0 Despus del perodo de prueba, los #[em hackers] ticos preparan un informe detallado. Esta documentacin incluye pasos para comprometer las vulnerabilidades descubiertas y pasos para parchearlas o mitigarlas.
      .col-lg-3.col-6.order-lg-2.order-1.mb-lg-0.mb-4(data-aos="fade-left")
        img(src="@/assets/curso/tema2/img07.png" alt="")

    .mb-4.d-flex
      .d-flex.flex-wrap.align-items-center.pe-4(style="background-color: #40327B; border-radius: 0 15px 15px 0;" data-aos="fade-right")
        img(src='@/assets/curso/temas/img03.svg' style="max-width: 40px;" alt="")
        .h3.col.p-2.text-white.mb-0 Cules son algunas limitaciones del #[em hacking] tico?

    .row.justify-content-center.align-items-center.mb-4
      .col-lg-7.order-lg-1.order-2(data-aos="fade-right")
        ul.lista-ul--color
          li
            i.fas.fa-check-circle(style="color: #583AE2")
            span #[b Alcance limitado]. Los #[em hackers] ticos no pueden progresar ms all de un alcance definido para que un ataque tenga xito. Sin embargo, no es descabellado discutir el potencial de ataque fuera del alcance con la organizacin.
          li
            i.fas.fa-check-circle(style="color: #583AE2")
            span #[b Restricciones de recursos]. Los piratas informticos maliciosos no tienen las limitaciones de tiempo que suelen enfrentar los piratas informticos ticos. El poder de cmputo y el presupuesto son restricciones adicionales de los #[em hackers] ticos.
          li
            i.fas.fa-check-circle(style="color: #583AE2")
            span #[b Mtodos restringidos]. Algunas organizaciones piden a los expertos que eviten los casos de prueba que provocan la cada de los servidores (por ejemplo, ataques de denegacin de servicio (DoS)).
      .col-lg-5.col-10.order-lg-2.order-1.mb-lg-0.mb-4(data-aos="fade-left")
        img(src="@/assets/curso/tema2/img08.png" alt="")

    separador
    #t_2_1.titulo-segundo.color-acento-contenido(data-aos="fade-right")
      h2 2.1 Etapas

    .row.justify-content-center.align-items-center.mb-5
      .col-lg-8.order-lg-1.order-2(data-aos="fade-right")
        p El #[em hacking] tico se ha convertido en un caballero de brillante armadura en el mundo del cibercriminal. Si bien los ataques cibernticos siguen proliferando en las organizaciones, las industrias y el mundo, la piratera tica brinda a las empresas la solucin para ayudarlas a combatir este problema cada vez mayor. Hay diferentes fases del #[em hacking] tico.
        p Las condiciones de seguridad de TI son tan graves que, en la mayora de los casos, las organizaciones tardan hasta 6 meses en darse cuenta de que ha habido una infraccin. Es por eso que necesitan trabajar con piratas informticos ticos para actualizar sus medidas de ciberseguridad y proteger sus datos.
        .BGR03.p-4
          p.mb-0 Las organizaciones necesitan medidas de seguridad de primer nivel para combatir con la gran cantidad de ataques que tienen lugar a diario. Se ha dicho que ocurre un ataque ciberntico cada 39 segundos, y los sistemas antiguos no estn lo suficientemente equipados para manejarlos a todos. Las actualizaciones peridicas y las actualizaciones de los sistemas de seguridad de TI son la mejor manera de proteger a las empresas contra este problema creciente.
      .col-lg-4.col-8.order-lg-2.order-1.mb-lg-0.mb-4(data-aos="fade-left")
        img(src="@/assets/curso/tema2/img09.svg" alt="")

    .mb-4.d-flex
      .d-flex.flex-wrap.align-items-center.pe-4(style="background-color: #40327B; border-radius: 0 15px 15px 0;" data-aos="fade-right")
        img(src='@/assets/curso/temas/img03.svg' style="max-width: 40px;" alt="")
        .h3.col.p-2.text-white.mb-0 Cules son las diferentes fases del #[em hacking] tico?

    .cajon-b.color-primario.cajon-end.p-3.mb-4(data-aos="fade-right")
      .row.justify-content-between
        .col-lg-10
          p.mb-0 Las organizaciones emplean #[em hackers] ticos para simular un ciberataque real en sus sistemas y redes. Este ataque viene en diferentes fases. Se necesita mucha habilidad y esfuerzo para que los piratas informticos ticos identifiquen todas las vulnerabilidades y las exploten para su mximo beneficio. Este ataque simulado se utiliza para identificar todas las reas de debilidad que enfrenta la organizacin para trabajar y fortalecerlas. Las fases del #[em hacking] tico son:
        .col-lg-2.col-3
          img(src='@/assets/curso/tema2/img10.svg' alt="")

    .row.justify-content-center.mb-5(data-aos="zoom-in")
      .col-lg-10
        AcordionA(tipo="b" clase-tarjeta="BGR05")
          .row.justify-content-around(titulo="1. La fase de reconocimiento")
            .col-lg-7.order-lg-1.order-2
              p Esta es la primera etapa en el proceso de piratera tica. El #[em hacker] de sombrero blanco recopila toda la informacin disponible sobre las redes y sistemas instalados, as como las medidas de seguridad que se han implementado. El #[em hacker] tico tiene dos tipos de reconocimiento que puede hacer en esta fase:
              ul.lista-ul--color
                li
                  i.fas.fa-check-circle(style="color: #583AE2")
                  p.mb-0 #[b El reconocimiento activo] busca informacin sobre el sistema de red, el servidor o la aplicacin de destino para aumentar las posibilidades de que se detecte al #[em hacker] en el sistema. Es mucho ms arriesgado que el segundo tipo de reconocimiento, que es el reconocimiento pasivo.
                li
                  i.fas.fa-check-circle(style="color: #583AE2")
                  p.mb-0 #[b El reconocimiento pasivo] es la forma ms sigilosa de obtener informacin sobre el objetivo. Esto se centra en la recopilacin de informacin sobre los miembros clave de la empresa, datos esenciales sobre la empresa, averiguar sus direcciones IP y buscar otros tipos de informacin crtica. Dado que la mayora de las organizaciones tienen casi toda su informacin pblica, la recopilacin pasiva de informacin puede ser muy fcil para un #[em hacker] tico.
            .col-lg-3.col-6.order-lg-2.order-1.mb-lg-0.mb-4
              img(src='@/assets/curso/tema2/img11.svg' alt="")
          .row.justify-content-around(titulo="2. La fase de exploracin")
            .col-lg-7.order-lg-1.order-2
              p La segunda fase en la estrategia de un #[em hacker] tico es la fase de exploracin. Este paso implica utilizar toda la informacin obtenida en la fase de reconocimiento y aplicarla para buscar vulnerabilidades en el rea objetivo. Hay diferentes tipos de escaneos realizados por #[em hackers] ticos. Pueden buscar puertos abiertos o diferentes servicios que se ejecutan sin proteccin en la organizacin.
              p Los #[em hackers] ticos tambin pueden:
              ul.lista-ul--color
                li
                  i.fas.fa-check-circle(style="color: #583AE2")
                  | Realizar escaneos de vulnerabilidades para encontrar errores en los servidores de la empresa, que pueden ser explotadas, este proceso se ha automatizado porque hay muchas herramientas disponibles para que los piratas informticos realicen anlisis de vulnerabilidades.
                li
                  i.fas.fa-check-circle(style="color: #583AE2")
                  p.mb-0 Crear varios 'mapas' de redes, este proceso de mapeo de red incluye encontrar el #[em firewall] utilizado por la organizacin y diferentes enrutadores y redes para ayudarlos durante su proceso de piratera.
            .col-lg-3.col-6.order-lg-2.order-1.mb-lg-0.mb-4
              img(src='@/assets/curso/tema2/img12.svg' alt="")
          .row.justify-content-around(titulo="3. La fase de obtencin de acceso")
            .col-lg-7.order-lg-1.order-2
              p.mb-0 Aqu es donde el #[em hacker] tico hace el hackeo real, utiliza toda la informacin obtenida y analizada de las dos fases anteriores para lanzar un ataque completo contra el sistema o la red en la que l intenta infiltrarse. Explota todas las vulnerabilidades expuestas y obtiene el control del sistema que ha sido pirateado. Ahora el #[em hacker] puede robar todos los datos que tiene disponibles, corromper los sistemas, agregar virus u otras entidades maliciosas, o manipularlos en su beneficio.
            .col-lg-3.col-6.order-lg-2.order-1.mb-lg-0.mb-4
              img(src='@/assets/curso/tema2/img13.svg' alt="")
          .row.justify-content-around(titulo="4. La fase de mantenimiento del acceso")
            .col-lg-7.order-lg-1.order-2
              p.mb-0 Por lo general, los piratas informticos tienen una misin que cumplir o un plan a seguir cuando piratean el sistema de una organizacin. Esto significa que simplemente ingresar o piratear el sistema no ser suficiente. El #[em hacker] tico tiene que mantener su acceso al servidor hasta que cumpla su objetivo. Los #[em hackers] ticos suelen emplear troyanos y otras puertas traseras o #[em rootkits] para llevar a cabo esta fase. Tambin pueden usar esta fase de acceso de mantenimiento para lanzar otros ataques para infligir ms dao a la organizacin.
            .col-lg-3.col-6.order-lg-2.order-1.mb-lg-0.mb-4
              img(src='@/assets/curso/tema2/img14.svg' alt="")
          .row.justify-content-around(titulo="5. La fase de cubrimiento de huellas")
            .col-lg-7.order-lg-1.order-2
              p Este es el paso final para completar todo el proceso de piratera tica. Si esta fase se completa con xito, el #[em hacker] tico ha logrado piratear un sistema o red. l podra infligir el mayor dao posible y ha logrado dejar el sistema sin dejar rastro. Necesitan cubrir sus huellas en todo momento para evitar ser detectados al entrar y salir de la red o el servidor. Los sistemas de seguridad existentes no deberan poder identificar al atacante. La seal de un ciberataque simulado exitoso es si el sistema de seguridad nunca se dio cuenta de que se produjo un ataque por completo.
              p Esto incluye muchas de las siguientes medidas que un #[em hacker] tico toma para ocultar y eliminar su presencia por completo:
            .col-lg-3.col-6.order-lg-2.order-1.mb-lg-0.mb-4
              img(src='@/assets/curso/tema2/img15.svg' alt="")
            .col-12.order-3
              .row.justify-content-center
                .col-lg-4.col-md-8.mb-4
                  .BGIMG03.p-3
                    .my-3.d-flex
                      .d-flex.flex-wrap.align-items-center
                        img(src='@/assets/curso/tema2/img16.svg' style="max-width: 90px;" alt="")
                        p.col.p-2.mb-0 Eliminacin de todos los registros.
                .col-lg-4.col-md-8.mb-4
                  .BGIMG03.p-3
                    .my-3.d-flex
                      .d-flex.flex-wrap.align-items-center
                        img(src='@/assets/curso/tema2/img16.svg' style="max-width: 90px;" alt="")
                        p.col.p-2.mb-0 Corrupcin de registros.
                .col-lg-4.col-md-8.mb-4
                  .BGIMG03.p-3
                    .my-3.d-flex
                      .d-flex.flex-wrap.align-items-center
                        img(src='@/assets/curso/tema2/img16.svg' style="max-width: 90px;" alt="")
                        p.col.p-2.mb-0 Modificar ciertos valores de #[em logs] o registros.
                .col-lg-4.col-md-8.mb-4
                  .BGIMG03.p-3
                    .my-3.d-flex
                      .d-flex.flex-wrap.align-items-center
                        img(src='@/assets/curso/tema2/img16.svg' style="max-width: 90px;" alt="")
                        p.col.p-2.mb-0 Eliminar todas las carpetas que ha creado el #[em hacker] tico.
                .col-lg-4.col-md-8.mb-4
                  .BGIMG03.p-3
                    .my-3.d-flex
                      .d-flex.flex-wrap.align-items-center
                        img(src='@/assets/curso/tema2/img16.svg' style="max-width: 90px;" alt="")
                        p.col.p-2.mb-0 Desinstalar todas las aplicaciones.
                .col-lg-4.col-md-8.mb-4
                  .BGIMG03.px-3.py-1
                    .my-3.d-flex
                      .d-flex.flex-wrap.align-items-center
                        img(src='@/assets/curso/tema2/img16.svg' style="max-width: 90px;" alt="")
                        p.col.p-2.mb-0 Eliminar todo rastro de cualquier actividad realizada por un #[em hacker] tico en el sistema o red.

    p El #[em hacking] tico es un proceso largo y difcil que involucra muchas fases. Los profesionales que trabajan deben tener la capacitacin y la certificacin necesarias en piratera tica y seguridad de TI para convertirse en piratas informticos ticos exitosos. Las fases requieren mucho tiempo, conocimiento y experiencia para llevarse a cabo y deben tomarse en serio. Las organizaciones tambin optan por personas certificadas sobre sus contrapartes no certificadas, porque estn a cargo de proteger los datos confidenciales y los activos digitales de la empresa.

    separador
    #t_2_2.titulo-segundo.color-acento-contenido(data-aos="fade-right")
      h2 2.2 Tcnicas

    .bloque-texto-g.color-primario.bloque-40.p-md-4.p-sm-3.p-2.mb-5
      .bloque-texto-g__img(
        :style="{'background-image': `url(${require('@/assets/curso/tema2/img17.jpg')})`}"
      )(data-aos="fade-right")
      .bloque-texto-g__texto.p-4(data-aos="fade-left")
        p.mb-0 El desarrollo de la tecnologa se puede ver en todas partes. Nadie puede negar que la tecnologa est creciendo a un ritmo acelerado. Sin embargo, a medida que avanza la tecnologa, existen problemas tcnicos. La piratera tica se utiliza para prevenir amenazas de fuentes desconocidas. Hay varios tipos de tcnicas de piratera tica. Uno puede aprender herramientas y tcnicas ticas a travs de varios cursos en lnea y fuera de lnea. Las herramientas se proporcionan para garantizar la seguridad de la informacin confidencial en la red y el sistema.

    .mb-4.d-flex
      .d-flex.flex-wrap.align-items-center.pe-4(style="background-color: #40327B; border-radius: 0 15px 15px 0;" data-aos="fade-right")
        img(src='@/assets/curso/temas/img03.svg' style="max-width: 40px;" alt="")
        .h3.col.p-2.text-white.mb-0 #[em Hacking] tico: un entendimiento

    .row.justify-content-center.align-items-center.mb-5
      .col-lg-8.order-lg-1.order-2(data-aos="fade-right")
        p La piratera tica implica un sistema de piratera que depende de valores ticos o morales sin mala intencin. Cualquier forma de piratera autorizada por el propietario del sistema de destino se conoce como piratera tica. Es el proceso de adaptar medidas de seguridad activas para defender los sistemas de piratas informticos con malas intenciones con respecto a la privacidad de los datos.
        p.mb-0 Las tcnicas de piratera tica proporcionan medidas de seguridad que un sistema aplica para buscar vulnerabilidades, infracciones y posibles amenazas a los datos. Un #[em hacker] tico piratea el sistema al que se ha dirigido antes que cualquier hacker. Por este motivo, se aplican parches de seguridad. Esto elimina y reduce efectivamente las posibilidades de que el atacante ejecute el #[em hack]. Utilizando herramientas y tcnicas de #[em hacking] tico, un #[em hacker] puede superar las amenazas buscando los puntos dbiles del sistema. Estas herramientas se pueden utilizar para proteger los datos y sistemas del usuario. Brindan seguridad y proteccin. Existen diferentes tipos de mtodos de #[em hacking] tico; algunos de ellos son los siguientes:
      .col-lg-4.col-8.order-lg-2.order-1.mb-lg-0.mb-4(data-aos="fade-left")
        img(src="@/assets/curso/tema2/img18.jpg" alt="")

    .row.justify-content-center.mb-4
      .col-xl-3.col-md-6.col-8.mb-xl-0.mb-4(data-aos="fade-right")
        .BGIMG07.hg-10.p-lg-5.p-4
          .row.justify-content-center.mb-4
            .col-lg-7.col-8
              img(src="@/assets/curso/tema2/img19.svg" alt="")
          .row.justify-content-center.mb-4
            .col-lg-7.col-8
              .h5.text-center.mb-0 #[em Hackers] de sombrero negro
      .col-xl-3.col-md-6.col-8.mb-xl-0.mb-4(data-aos="flip-right")
        .BGIMG07.hg-10.p-lg-5.p-4
          .row.justify-content-center.mb-4
            .col-lg-7.col-8
              img(src="@/assets/curso/tema2/img20.svg" alt="")
          .row.justify-content-center.mb-4
            .col-lg-7.col-8
              .h5.text-center.mb-0 #[em Hackers] de sombrero blanco
      .col-xl-3.col-md-6.col-8.mb-xl-0.mb-4(data-aos="flip-left")
        .BGIMG07.hg-10.p-lg-5.p-4
          .row.justify-content-center.mb-4
            .col-lg-7.col-8
              img(src="@/assets/curso/tema2/img21.svg" alt="")
          .row.justify-content-center.mb-4
            .col-lg-7.col-8
