          .row.justify-content-center.mb-4
            .col-lg-7.col-8
              .h5.text-center.mb-0 Varios piratas informticos.


    figure.my-5(data-aos="zoom-in")
      .video
        iframe(width="560" height="315" src="https://www.youtube.com/embed/Puk3JZ5R_Ic" title="Tipos Hackers" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen)
      figcaption Video. Tipos Hackers

    p.mb-5 Los verificadores de sombrero blanco son piratas informticos ticos, mientras que los piratas informticos de sombrero negro se denominan piratas informticos o #[em crackers] no autorizados. Utilizan diversas tcnicas y mtodos para proteger e interrumpir los sistemas de seguridad. Se puede recopilar la mayor cantidad de datos posible sobre sistemas y redes especficas a travs de tcnicas de huella y piratera tica. El paquete para un novato depende de la habilidad y el conocimiento. Un #[em hacker] experimentado puede obtener buenos ingresos. Existe una gran demanda de #[em ethical hacking] en el mercado, y se est volviendo cada vez ms popular. Si alguien quiere sobresalir en este campo, puede elegir la capacitacin en #[em hacking] tico en lnea.

    .mb-4.d-flex
      .d-flex.flex-wrap.align-items-center.pe-4(style="background-color: #40327B; border-radius: 0 15px 15px 0;" data-aos="fade-right")
        img(src='@/assets/curso/temas/img03.svg' style="max-width: 40px;" alt="")
        .h3.col.p-2.text-white.mb-0 Las mejores tcnicas de piratera tica

    p La piratera tica tiene el potencial de probar, escanear y asegurar sistemas y datos. Las tcnicas de piratera tica se pueden aprender utilizando un PDF de piratera tica y algunas de las tcnicas se enumeran a continuacin:

    .BGIMG11.p-4.mb-5(data-aos="zoom-in")
      LineaTiempoC.color-primario(text-small)
        .row(titulo="1")
          .col-lg-8.order-lg-1.order-2
            .h4 Suplantacin de identidad
            p.mb-0 El #[em phishing] es un ataque de ciberseguridad en el que un #[em hacker] enva mensajes hacindose pasar por una persona de confianza. Estos tipos de mensajes manipulan a un usuario, lo que hace que realice acciones como instalar un archivo malicioso y hacer clic en un enlace. Un #[em phisher] utiliza recursos pblicos para recopilar informacin sobre la experiencia personal y laboral de la vctima. Luego usan esta informacin para crear un mensaje falso confiable.
          .col-lg-4.col-8.order-lg-2.order-1.mb-lg-0.mb-4
            img(src="@/assets/curso/tema2/img23.svg" alt="")
        .row(titulo="2")
          .col-lg-8.order-lg-1.order-2
            .h4 Rastrear
            p.mb-0 La deteccin es el proceso de realizar un seguimiento y capturar todos los paquetes que pasan a travs de una red determinada. Esto se hace usando algunas herramientas de rastreo. Tambin se conoce como escuchas telefnicas, ya que puede escuchar y conocer la conversacin. Un #[em sniffer] cambia la NIC del sistema al modo promiscuo.
          .col-lg-4.col-8.order-lg-2.order-1.mb-lg-0.mb-4
            img(src="@/assets/curso/tema2/img24.svg" alt="")
        .row(titulo="3")
          .col-lg-8.order-lg-1.order-2
            .h4 Ingeniera social
            p.mb-0 La ingeniera social se utiliza para convencer a las personas de que revelen su informacin confidencial. El atacante engaa a las personas aprovechndose de su confianza y falta de conocimiento. Hay tres tipos de ingeniera social: basada en humanos, en dispositivos mviles y en computadoras. Debido a las polticas de seguridad laxas y la ausencia de herramientas de #[em hardware] o #[em software] para evitarlo, es difcil detectar un ataque de ingeniera social.
          .col-lg-4.col-8.order-lg-2.order-1.mb-lg-0.mb-4
            img(src="@/assets/curso/tema2/img25.svg" alt="")
        .row(titulo="4")
          .col-lg-8.order-lg-1.order-2
            .h4 Huella
            p.mb-0 En esta tcnica de piratera tica, el pirata informtico recopila la mayor cantidad de datos posible sobre un sistema e infraestructura, para reconocer oportunidades para penetrarlos. El pirata informtico puede usar varias herramientas y tecnologas para obtener informacin y descifrar un sistema completo.
          .col-lg-4.col-8.order-lg-2.order-1.mb-lg-0.mb-4
            img(src="@/assets/curso/tema2/img26.svg" alt="")
        .row(titulo="5")
          .col-lg-8.order-lg-1.order-2
            .h4 Inyeccin SQL
            p.mb-0 La inyeccin SQL es un ataque en el que el atacante enva una consulta SQL, una declaracin, a un servidor de base de datos que la modifica segn sea necesario. Una inyeccin SQL ocurre cuando la entrada del usuario se sanea incorrectamente antes de usarla en una consulta SQL. SQL permite asegurar una respuesta de la base de datos. Ayudar al #[em hacker] a comprender la construccin de la base de datos, como los nombres de las tablas.
          .col-lg-4.col-8.order-lg-2.order-1.mb-lg-0.mb-4
            img(src="@/assets/curso/tema2/img27.svg" alt="")
        .row(titulo="6")
          .col-lg-8.order-lg-1.order-2
            .h4 Enumeracin
            p.mb-0 Enumeracin tambin significa recopilacin de informacin. En este proceso, el atacante crea una conexin con la vctima para encontrar tantos vectores de ataque que se utilicen para explotar el sistema en el futuro. Un #[em hacker] necesita establecer una conexin activa con el #[em host] de destino. En primer lugar, se cuentan y evalan las vulnerabilidades. Luego, se hace para buscar ataques y amenazas para apuntar al sistema. Esto se utiliza para recopilar el nombre de usuario, los nombres de #[em host], las contraseas y las direcciones IP.
          .col-lg-4.col-8.order-lg-2.order-1.mb-lg-0.mb-4
            img(src="@/assets/curso/tema2/img28.svg" alt="")

    .mb-4.d-flex
      .d-flex.flex-wrap.align-items-center.pe-4(style="background-color: #40327B; border-radius: 0 15px 15px 0;" data-aos="fade-right")
        img(src='@/assets/curso/temas/img03.svg' style="max-width: 40px;")
        .h3.col.p-2.text-white.mb-0 Herramientas para ejecutar las tcnicas perfectas de piratera tica

    p Hay muchas herramientas de piratera tica disponibles para la comodidad del usuario. Adems, las herramientas de piratera tica ayudan en las investigaciones de seguridad.

    .BGIMG21.p-4.mb-5
      .row.justify-content-center.align-items-center
        .col-lg-4.col-8.mb-lg-0.mb-4(data-aos="fade-right")
          img(src='@/assets/curso/tema2/img29.svg' alt="")
        .col-lg-8(data-aos="fade-left")
          SlyderF(columnas="col-lg-6")
            .BGR04.hg-11.p-xl-3.p-2
              .row.justify-content-center.align-items-end.mb-4
                .col-4
                  img(src='@/assets/curso/tema2/img30.svg' alt="")
                .col-8
                  .h4 1. Ettercap
              p.mb-0 Ettercap incluye las funciones de anlisis de #[em host] y red. Adems, Ettercap tiene la capacidad de rastrear una conexin SSH. Permite crear complementos personalizados utilizando API. Adems, permitir inyectar algunos caracteres en el servidor o en la red del cliente; tambin admite un anlisis detallado de la accin junto con protocolos pasivos. Uno puede solicitar un programa de certificado de seguridad ciberntica en lnea para aprender una gestin y control de seguridad efectivos.
            .BGR04.hg-11.p-xl-3.p-2
              .row.justify-content-center.align-items-end.mb-4
                .col-4
                  img(src='@/assets/curso/tema2/img31.svg' alt="")
                .col-8
                  .h4 2. Netsparker
              p.mb-0 Netsparker es el escner de seguridad de aplicaciones web ms reciente que detecta automticamente las vulnerabilidades en las aplicaciones web. Est disponible en forma de solucin SAAS. Netsparker detecta vulnerabilidades muertas utilizando la ltima tecnologa de escaneo. La herramienta requiere menos configuracin; puede escanear ms de 1.000 aplicaciones web en poco tiempo.
            .BGR04.hg-11.p-xl-3.p-2
              .row.justify-content-center.align-items-end.mb-4
                .col-4
                  img(src='@/assets/curso/tema2/img32.svg' alt="")
                .col-8
                  .h4 3. Burp suite
              p.mb-0 Burp Suite es una de las herramientas de piratera tica que ayuda en las pruebas de seguridad. Esta caracterstica es til para probar aplicaciones web. Incluye una amplia gama de herramientas que ayudan en el proceso de prueba. La herramienta Burp Suite puede detectar el spam de alrededor de 2.000 aplicaciones web. Tambin puede escanear aplicaciones de #[em software] de cdigo abierto. Se utilizan para detectar errores y #[em malware] con precisin con la ayuda de herramientas de anlisis avanzadas.
            .BGR04.hg-11.p-xl-3.p-2
              .row.justify-content-center.align-items-end.mb-4
                .col-4
                  img(src='@/assets/curso/tema2/img33.svg' alt="")
                .col-8
                  .h4 4. John the Ripper
              p.mb-0 John the Ripper es una de las herramientas ms populares para descifrar contraseas. La herramienta se utiliza para probar la seguridad de la contrasea. Esta herramienta utiliza tecnologa de fuerza bruta para hackear contraseas, puede detectar automticamente el tipo de cifrado de la contrasea. Esta funcin la convierte en la mejor entre todas las dems herramientas para hackear claves. Esta herramienta utiliza algoritmos como MD4, LDAP, DES y Hash LM.
            .BGR04.hg-11.p-xl-3.p-2
              .row.justify-content-center.align-items-end.mb-4
                .col-4
                  img(src='@/assets/curso/tema2/img34.svg' alt="")
                .col-8
                  .h4 5. Nmap
              p.mb-0 Nmap es una herramienta de seguridad de cdigo abierto; esta se utiliza principalmente para gestionar y auditar la seguridad de redes y sistemas. Por lo general, los profesionales de seguridad de la informacin usan esta herramienta para encontrar #[em malware], auditoras de red, mapeo de red y ms para #[em hosts] locales y remotos.
            .BGR04.hg-11.p-xl-3.p-2
              .row.justify-content-center.align-items-end.mb-4
                .col-4
                  img(src='@/assets/curso/tema2/img35.svg' alt="")
                .col-8
                  .h4 6. Wireshark
              p.mb-0 Wireshark se utiliza para analizar el trfico de red en tiempo real. Esta herramienta es de cdigo abierto para la piratera tica. Se incluyen diferentes funciones como GUI de potencia y navegador de paquetes, lo que da como resultado otros formatos. Adems, la herramienta admite varios tipos de protocolos. Est disponible para diferentes sistemas operativos como Windows, Mac, etc.
            .BGR04.hg-11.p-xl-3.p-2
              .row.justify-content-center.align-items-end.mb-4
                .col-4
                  img(src='@/assets/curso/tema2/img36.svg' alt="")
                .col-8
                  .h4 7. OpenVAS
              p.mb-0 Open VAS se utiliza para detectar vulnerabilidades en diferentes #[em hosts]. Es uno de los escneres de red de cdigo abierto. En esta herramienta se incluyen diferentes funciones, como una interfaz basada en web, escaneos programados y escaneo de mltiples #[em hosts] a la vez. Adems, OpenVas est integrado con el #[em software] de monitoreo Nagios.
            .BGR04.hg-11.p-xl-3.p-2
              .row.justify-content-center.align-items-end.mb-4
                .col-4
                  img(src='@/assets/curso/tema2/img37.svg' alt="")
                .col-8
                  .h4 8. Escner Angry IP
              p.mb-0 El escner Angry IP no requiere ninguna instalacin. La herramienta escanea redes locales y web. Angry IP cuenta con las mejores tcnicas de escaneo. La herramienta es de cdigo abierto y gratuita, que admite diferentes plataformas. La herramienta ayuda a los piratas informticos con soporte exclusivo.
            .BGR04.hg-11.p-xl-3.p-2
              .row.justify-content-center.align-items-end.mb-4
                .col-4
                  img(src='@/assets/curso/tema2/img38.svg' alt="")
                .col-8
                  .h4 9. Iron
              p.mb-0 La herramienta Iron es til para las pruebas de #[em malware] de aplicaciones web. Es de cdigo abierto y gratuito. Adems, la herramienta es una herramienta basada en GUI fcil de usar. Los lenguajes de programacin como Python y Ruby son compatibles con l. Esta herramienta proporciona informes en diferentes formatos como HTML y RTF. Esta herramienta puede comprobar cerca de 30 aplicaciones web.
            .BGR04.hg-11.p-xl-3.p-2
              .row.justify-content-center.align-items-end.mb-4
                .col-4
                  img(src='@/assets/curso/tema2/img39.svg' alt="")
                .col-8
                  .h4 10. Acunetix
              p.mb-0 La herramienta Acunetix es una herramienta de piratera completamente automtica. Esta herramienta se adelanta a cualquier intruso. Los problemas complejos relacionados con la web y la red se auditan en la herramienta. Varias caractersticas incluyen escanear diferentes variantes como inyeccin SQL, XSS, etc. Estn disponibles tanto en las instalaciones como en plataformas en la nube.

    .mb-4.d-flex
      .d-flex.flex-wrap.align-items-center.pe-4(style="background-color: #40327B; border-radius: 0 15px 15px 0;" data-aos="fade-right")
