    p A continuacin, se muestra la lista de diferentes tipos de #[em Hacking] tico el cual hace referencia a la prctica que hace una persona altamente formada y con grandes conocimientos sobre informtica y ciberseguridad, para ayudar a una empresa a detectar vulnerabilidades y debilidades:

