        .row.justify-content-around.align-items-center
          .col-lg-5.order-lg-1.order-2
            .h4 Hackeo de aplicaciones web
            p.mb-0 Los mtodos utilizados son ataques de inyeccin SQL, secuencias de comandos entre sitios, comunicaciones inseguras, etc.
