        .row.justify-content-around.align-items-center
          .col-lg-5.order-lg-1.order-2
            .h4 Hackeo del sistema
            p.mb-0 La piratera del sistema es el sacrificio del #[em software] de la computadora para acceder y robar sus datos confidenciales. El #[em hacker] se aprovecha de las debilidades de un sistema informtico para obtener la informacin y los datos y se aprovecha injustamente.
          .col-lg-5.col-10.order-lg-2.order-1.mb-lg-0.mb-4
