            .h4 Hackeo de redes inalmbricas
            p.mb-0 La piratera inalmbrica ataca redes inalmbricas o puntos de acceso que ofrecen informacin confidencial, como ataques de autenticacin, acceso al portal de administracin, contrasea wifi y otros datos similares. Se realiza para acceder a una red wifi privada.
          .col-lg-5.col-10.order-lg-2.order-1.mb-lg-0.mb-4
