    separador
    #t_2_3.titulo-segundo.color-acento-contenido(data-aos="fade-right")
      h2 2.3 Consideraciones

    .row.justify-content-center.align-items-center.mb-4
      .col-lg-4.col-8.mb-lg-0.mb-4(data-aos="fade-right")
        img(src="@/assets/curso/tema2/img45.png" alt="")
      .col-lg-8(data-aos="fade-left")
        p La piratera tica se ha convertido en una forma esencial para que las empresas identifiquen y aborden las exposiciones a la seguridad ciberntica. En este aparte de piratera tica, los especialistas en seguridad ciberntica con sede en el Reino Unido, Redscan, describen la prctica como la identificacin y explotacin de vulnerabilidades de seguridad ciberntica en entornos de TI con fines legtimos y no maliciosos.
        p.mb-0 La piratera tica es lo opuesto a la piratera de "sombrero negro", el tipo de piratera que aparece en los titulares de las noticias por las razones equivocadas. La piratera de sombrero negro es un delito y, si bien la piratera tica puede involucrar tcnicas similares, generalmente la lleva a cabo una empresa profesional contratada para realizar pruebas y se adhiere a los ms altos estndares. Qu hace que la piratera tica sea "tica"? Veamos cmo la piratera tica puede ayudar a proteger a las empresas de los ataques, adems de examinar las formas en que puede asegurarse de que est trabajando con piratas informticos ticos genuinos.

    .row.justify-content-center.mb-4(data-aos="zoom-in")
      AcordionA(tipo="a" clase-tarjeta="BGR05")
        .row.justify-content-center(titulo="Realizado con consentimiento")
          .col-lg-7.order-lg-1.order-2
            p.mb-0 El #[em hacking] tico siempre se realiza con consentimiento. Si bien el objetivo de los compromisos es reproducir con precisin las tcticas, tcnicas y procedimientos utilizados por los ciberdelincuentes, nunca est diseado para ser malicioso y tiene como objetivo evitar daos e interrupciones en las empresas. Antes de realizar una evaluacin, una firma profesional de ciberseguridad se asegurar de que exista un acuerdo formal que defina claramente el alcance de las evaluaciones y mantenga la confidencialidad del cliente.
          .col-lg-4.col-8.order-lg-2.order-1.mb-lg-0.mb-4
            img(src='@/assets/curso/tema2/img46.png' alt="")
        .row.justify-content-center(titulo="Realizado por expertos")
          .col-lg-7.order-lg-1.order-2
            p.mb-0 La piratera tica siempre debe ser realizada por profesionales capacitados que comprendan las ltimas herramientas y tcnicas de piratera y realicen evaluaciones con los ms altos estndares tcnicos, legales y ticos. Es importante buscar organizaciones que tengan las certificaciones de piratera tica adecuadas: uno de los organismos de acreditacin ms conocidos y reconocidos es Crest. Tambin es recomendable buscar firmas que tengan personal certificado con una amplia gama de disciplinas de piratera tica; esto demuestra la capacidad de la organizacin para realizar una amplia gama de evaluaciones.
          .col-lg-4.col-8.order-lg-2.order-1.mb-lg-0.mb-4
            img(src='@/assets/curso/tema2/img47.png' alt="")
        .row.justify-content-center(titulo="Realizado por consultores con autorizacin de seguridad")
          .col-lg-7.order-lg-1.order-2
            p.mb-0 Al encargar una evaluacin de piratera tica, es importante tener plena confianza en las personas involucradas. Cuando una prueba de penetracin implica el acceso a informacin altamente confidencial o clasificada, las empresas pueden considerar salvaguardas adicionales, como el uso de evaluadores con autorizacin de seguridad de alto nivel.
          .col-lg-4.col-8.order-lg-2.order-1.mb-lg-0.mb-4
            img(src='@/assets/curso/tema2/img48.png' alt="")
        .row.justify-content-center(titulo="Realizado de acuerdo con las leyes vigentes")
          .col-lg-7.order-lg-1.order-2
            p.mb-0 Hay muchos aspectos legales que deben tenerse en cuenta al someterse a una piratera tica. Los evaluadores pueden, a travs del proceso normal de un compromiso, acceder a datos altamente confidenciales. Para lograr un objetivo acordado, es posible que tengan la necesidad de filtrar esta informacin. Un negocio de piratera tica profesional considerar los problemas legales descritos en la legislacin, incluidas las leyes en pases/estados especficos y regulaciones como GDPR. Al programar cualquier forma de piratera tica, es aconsejable consultar al equipo legal de la organizacin para asegurarse de que las pruebas se mantengan dentro de lo permitido por la ley. Si bien ningn #[em hacker] tico tiene la intencin de causar daos o interrupciones, existen riesgos inherentes a la realizacin de pruebas en sistemas en vivo: todas las partes deben ser conscientes de los riesgos y establecer las medidas de seguridad adecuadas.
          .col-lg-4.col-8.order-lg-2.order-1.mb-lg-0.mb-4
            img(src='@/assets/curso/tema2/img49.png' alt="")
        .row.justify-content-center(titulo="Realizado de forma transparente")
          .col-lg-7.order-lg-1.order-2
            p.mb-0 Es esencial que las evaluaciones de piratera tica sean lo ms transparente posible. Un #[em hacker] tico siempre compartir los hallazgos y ofrecer consejos de reparacin para garantizar que las vulnerabilidades se informen y aborden. Deben ser localizables a lo largo de los compromisos y proporcionar informes escritos claros para resumir los hallazgos y las recomendaciones. Hay muchas cosas a considerar al encargar la piratera tica para el negocio. En cualquier caso, es una buena idea trabajar con un proveedor con mucha experiencia que est feliz de explicarle cualquier riesgo y asegurarse de que todo el proceso se lleve a cabo de la manera ms segura posible y brinde resultados tangibles.
          .col-lg-4.col-8.order-lg-2.order-1.mb-lg-0.mb-4
            img(src='@/assets/curso/tema2/img50.png' alt="")

